
.cat-main {
  background-color: rgb(159, 242, 242);
  height: 90vh;
  display: flex;
  flex-direction: row-reverse;
  /* border: 2px solid red; */
}

.c-main-right {
  border-left: 2px solid red;
  margin: auto;
  width: 70%;
}
.c-main-left {
  margin: auto;
  /* border: 2px solid red; */
  width: 30%;
  text-align: center;
  align-items: center;
}

.button {
  width: 100%;
}
.c-table {
  width: 50%;
  margin: auto;
}

.c-main-left .create {
  align-items: end;
  margin-right: 10vh;
}

.main-product {
  height: 80vh;
  padding: 12px;
  display: flex;
}
.right-product {
  height: 70%;
  padding: 12px;
  width: 80%;
  /* border-bottom: 2px solid red; */
  margin: auto;
}
.left-product {
  /* padding: 12vh; */
  border: 1px solid red;
  /* width: 100%; */
}
.product-form {
  width: 50%;
  margin: auto;
}
.product-form label {
  width: 30%;
}
.AllUsers {
  height: 85vh;
  display: flex;
}
.right-AllUsers {
  margin: auto;
  width: 50%;
  /* padding: 12px; */
  /* border: 1px solid black; */
  /* align-items: center; */
  justify-content: center;
}
.right-AllUsers table {
  width: 80%;
  margin: 12px auto;
  /* border: 2px solid red; */
}

.right-AllUsers table thead {
  border-top: 3px solid red;
  border-bottom: 3px solid red;
  border-left: 2px solid red;
  border-right: 2px solid red;
  border-top-left-radius: 30px;
}
.right-AllUsers table tbody td {
  padding: 12px;
  border: 1px solid black;
}
.main-products {
  height: 85vh;
}
@media only screen and (max-width: 700px) {
  .main-product {
    display: flex;
    flex-direction: column;
  }
  .left-product {
    padding: 12vh;
    border: 1px solid red;
    width: 100%;
  }
  .right-product {
    width: 100%;
    /* border: 2px solid red; */
  }
  .u-left {
    margin: auto;
    width: 30%;
    border: 0px;
  }
}

.AllCategories {
  display: flex;
  flex-direction: row;
}
.right-categories {
  width: 80%;
  margin: auto;
}
.right-categories h3 {
  margin: 6px;
}
.right-categories .card {
  width: 40vh;
  margin-left: 32px;
  margin-bottom: 4px;

  /* margin:4px auto;   */
}
.right-categories .categ {
  width: 100%;
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.c-products {
  width: 100%;
  margin-top: 15vh;
  /* height: 500px; */
  /* border: 2px solid rgb(12, 7, 7); */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.c-card img {
  height: 150px;
  border-radius: 30px;
  width: 100%;
  /* border: 1px solid red; */
}
.c-card {
    /* height: 35vh; */
  background-color: #fff;
  padding: 5px;
  border: 2px solid rgb(0, 0, 0);
  width: 26vh;
  border-radius: 20px;
}
