.login-form {
  border-right: 2px solid rgb(175, 111, 111);
  border-left: 2px solid rgb(26, 240, 23);
  border-radius: 30px;
  width: 40%;
  height: 50%;
  margin: 25vh auto;
  padding: 13px;
  box-shadow: 0px 0px 2px 2px;
}
.registration-form {
  border-right: 2px solid rgb(175, 111, 111);
  border-left: 2px solid rgb(26, 240, 23);
  border-radius: 30px;
  width: 30%;
  /* height: 50%; */
  margin: 12vh auto;
  padding: 12px;
  box-shadow: 0px 0px 2px 2px;
}
.form-control {
  /* box-sizing: border-box; */
  box-shadow: 2px 2px black;
}

.background {
  padding-bottom: 2px;
  padding-top: 2px;
  /* height: 100%; */
  box-sizing: border-box;
  background: url("../../public/image/banner.jpg");
  background-size: cover;
  background-repeat: no-repeat;  
}
.form-label {
  margin: 10px;
  color: white;
}
.c-main {
  height: 100% !important;
}
.btn-login{
  margin-top: 2vh !important;
  height: 5vh;
  /* font-size: 0.5rem; */
}


@media screen and (max-width:800px){
  .btn-login{
    margin-top: 2vh !important;
    height: 5vh;
    font-size: 0.5rem;
  }
  .registration-form{
    width: 60%;
    margin: 8vh auto;

  } .login-form{
    width: 60%;
    margin: 8vh auto;

  }
}